<template>
  <div>
    <v-toolbar dense height="60px" color="error">
      <v-toolbar-title>
        <router-link to="/">
          <v-btn icon><v-icon size="35" color="white">mdi-home</v-icon></v-btn>
        </router-link>
      </v-toolbar-title>
    </v-toolbar>
    <v-container fluid class="bg-light">
      <v-row align-content="center">
        <v-col cols="12" lg="7">
          <div v-if="getimage.business_reg_img !== null">
            <img :src=url+getimage.business_reg_img class="img-fluid">
          </div>
          <div v-else>
            <img src="@/assets/business-register.jpg" class="img-fluid">
          </div>
        </v-col>
        <v-col lg="5">
          <v-layout align-center justify-center>
            <v-form ref="form" v-model="valid" lazy-validation>
              <v-card class="elevation-18">
                <v-toolbar flat color="grey">
                  <v-toolbar-title class="mx-auto text-white">List Your Business</v-toolbar-title>
                </v-toolbar>
                <v-card-text>
                  <v-text-field type="number" v-model="formData.user_role" class="d-none"></v-text-field>
                  <v-text-field type="number" v-model="formData.user_status" class="d-none"></v-text-field>
                  <v-row align-content="center">
                    <v-col cols="12" md="6">
                      <v-text-field
                          v-model="formData.name"
                          :rules="nameRules"
                          :counter="100"
                          class="my-0 py-0"
                          label="Store / Business Name" type="text"
                          prepend-inner-icon="mdi-account">
                      </v-text-field>
                    </v-col>
                    <v-col cols="12" md="6">
                      <v-text-field class="my-0 py-0"
                                    v-model="formData.username"
                                    :rules="usernameRules"
                                    @keyup="userNameVerify"
                                    :error-messages="userNameVerifyMessage"
                                    :counter="80"
                                    label="Username"
                                    type="text"
                                    prepend-inner-icon="mdi-account">
                      </v-text-field>
                    </v-col>
                  </v-row>
                  <v-row align-content="center">
                    <v-col cols="12" md="6">
                      <v-text-field class="my-0 py-0"
                                    v-model="formData.email"
                                    :rules="emailRules"
                                    @keyup="emailVerify"
                                    :error-messages="emailVerifyMessage"
                                    label="Your Email"
                                    type="email"
                                    prepend-inner-icon="mdi-email">
                      </v-text-field>
                    </v-col>
                    <v-col cols="12" md="6">
                      <v-text-field class="my-0 py-0"
                                    v-model="formData.mobile_phone"
                                    :rules="mobileRules"
                                    @keyup="phoneVerify"
                                    :error-messages="phoneVerifyMessage"
                                    :counter="10"
                                    label="Enter Mobile"
                                    type="number"
                                    prepend-inner-icon="mdi-cellphone">
                      </v-text-field>
                    </v-col>
                  </v-row>
                  <v-row align-content="center">
                    <v-col cols="12" md="6">
                      <v-autocomplete class="my-0 py-0"
                                      v-model="stateId"
                                      @change="getCities"
                                      :rules="stateRules"
                                      label="Select State"
                                      :items="allstates"
                                      item-text="state"
                                      prepend-inner-icon="mdi-home-city"
                                      item-value="id">
                      </v-autocomplete>
                    </v-col>
                    <v-col cols="12" md="6">
                      <v-autocomplete class="my-0 py-0"
                                      v-model="formData.city_id"
                                      :rules="cityRules"
                                      label="Select City"
                                      :items="cities"
                                      item-text="city"
                                      prepend-inner-icon="mdi-city"
                                      item-value="id"
                                      :no-data-text="customNoDataText">
                      </v-autocomplete>
                    </v-col>
                  </v-row>
                  <v-row align-content="center">
                    <!--                  <v-col cols="12" md="6">-->
                    <!--                    <v-text-field class="my-0 py-0"-->
                    <!--                                  v-model="formData.referral_code"-->
                    <!--                                  :rules="referralCodeRules"-->
                    <!--                                  :counter="6"-->
                    <!--                                  maxlength="6"-->
                    <!--                                  label="Enter Refferal Code"-->
                    <!--                                  type="number"-->
                    <!--                                  prepend-inner-icon="mdi-all-inclusive"-->
                    <!--                    ></v-text-field>-->
                    <!--                  </v-col>-->
                    <v-col cols="12" md="6">
                      <v-autocomplete class="my-0 py-0"
                                      v-model="formData.cat_id"
                                      @change="AllCats"
                                      :rules="maincatRuless"
                                      label="Select Main Category"
                                      :items="allmaincategories"
                                      item-text="cat_name"
                                      item-value="id"
                                      prepend-inner-icon="mdi-tray-arrow-down">
                      </v-autocomplete>
                    </v-col>
                    <v-col cols="12" md="6">
                      <v-autocomplete class="my-0 py-0"
                                      v-model="formData.subcategory_id"
                                      :rules="catRuless"
                                      label="Select Sub Category"
                                      :items="allcategories"
                                      item-text="subcat_name"
                                      multiple
                                      item-value="id"
                                      :no-data-text="customNoDataText1"
                                      prepend-inner-icon="mdi-tray-arrow-down">
                      </v-autocomplete>
                    </v-col>
                  </v-row>
                  <v-row align-content="center">
                    <v-col cols="12" md="6">
                      <v-text-field class="my-0 py-0"
                                    v-model="formData.password"
                                    :rules="passwordRules"
                                    :counter="15"
                                    label="Enter Password"
                                    type="password"
                                    prepend-inner-icon="mdi-lock">
                      </v-text-field>
                    </v-col>
                    <v-col cols="12" md="6">
                      <v-text-field class="my-0 py-0"
                                    v-model="formData.confirm_password"
                                    :rules="[(formData.password === formData.confirm_password) || 'Confirm password does not match']"
                                    :counter="15"
                                    label="Confirm Password"
                                    type="password"
                                    prepend-inner-icon="mdi-lock">
                      </v-text-field>
                    </v-col>
                  </v-row>
                  <v-row align-content="center">
                    <v-col cols="12" md="12">
                      <v-textarea class="my-0 py-0"
                                  v-model="formData.address"
                                  :rules="addressRules"
                                  :counter="100"
                                  label="Enter Address"
                                  rows="2"
                                  prepend-inner-icon="mdi-map">
                      </v-textarea>
                    </v-col>
                  </v-row>
                  <v-row class="mt-0">
                    <v-col cols="12" md="5">
                      <v-checkbox type="checkbox"
                                  label="Agree to"
                                  v-model="formData.agree_policy"
                                  :rules="agree_policyRules"
                                  value="1"
                                  class="my-0 py-0">
                        <template v-slot:label>
                          <div>
                            Agree to
                            <v-tooltip bottom>
                              <template v-slot:activator="{ on }">
                                <a target="_blank" href="/terms-of-use" @click.stop v-on="on" class="vcart-right-section">
                                  Terms of Use
                                </a>
                              </template>
                              Opens in new window
                            </v-tooltip>
                          </div>
                        </template>
                      </v-checkbox>
                    </v-col>
                  </v-row>
                </v-card-text>
                <v-card-actions class="cart-btn-btm">
                  <v-btn :disabled="!valid" @click="BusinessformSubmit" color="red" large block class="text-white">Register</v-btn>
                </v-card-actions>
              </v-card>
            </v-form>
            <v-snackbar v-model="snackbar" :timeout="timeout">{{ text }}
              <template v-slot:action="{}">
                <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">Close</v-btn>
              </template>
            </v-snackbar>
          </v-layout>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "BusinessRegister",
  metaInfo: {title: 'Business Register'},
  data() {
    return{
      formData:{
        name: '',
        username: '',
        subcategory_id: '',
        cat_id: '',
        mobile_phone: '',
        state_id: '',
        city_id: '',
        email:'',
        password: '',
        confirm_password:'',
        address: '',
        agree_policy: '',
        user_role: 1,
        user_status: 1,
      },
      getimage:[],
      url:'https://bizlx.itechvision.in',
      // url:'http://127.0.0.1:8000',
      allcategories:[],
      allmaincategories:[],
      allstates:[],
      stateId: '',
      // catId: '',
      cities:[],
      userNameVerifyMessage:'',
      emailVerifyMessage:'',
      phoneVerifyMessage:'',
      valid: false,
      snackbar: false,
      text: '',
      timeout: '',
      customNoDataText: 'Please Select State First',
      customNoDataText1: 'Please Select Main Category First',
      nameRules: [
        v => !!v || 'Store/Bussine Name is required.',
        v => v.length <= 100 || 'Name must be less than 100 characters.',
      ],
      usernameRules: [
        v => !!v || 'Username is required.',
        v => v.length <= 80 || 'Username must be less than 80 characters.',
        v => (v.split(' ').length <= 1) || 'space not allowed',
        v => /^[\w\s]+$/.test(v) || 'Username may only contain letters, numbers, dashes and underscores'
      ],
      catRuless: [
        v => !!v || 'Sub Category name is required.',
      ],
      maincatRuless: [
        v => !!v || 'Main Category name is required.',
      ],
      mobileRules: [
        v => !!v || 'Mobile number is required.',
        v => v.length == 10 || 'Mobile number must be 10 characters.',
      ],
      stateRules: [
        v => !!v || 'State is required.',
      ],
      cityRules: [
        v => !!v || 'City is required.',
      ],
      emailRules: [
        v => !!v || 'E-mail is required.',
        v => /.+@.+/.test(v) || 'E-mail must be valid.',
      ],
      passwordRules: [
        v => !!v || 'Password is required.',
        v => /[A-Z]/.test(v) || 'Use upper case.',
        v => /[a-z]/.test(v) || 'Use lower case.',
        v => /[0-9]/.test(v) || 'Use number.',
        v => /[#?!@$%^&*-]/.test(v) || 'Use special character.',
        v => v.length >= 8 || 'Password must be greater than 8 characters.',
        v => v.length <= 15 || 'Password must be less than 15 characters.',
      ],
      addressRules: [
        v => !!v || 'Address is required.',
        v => v.length <= 100 || 'Address must be less than 100 characters.',
      ],
      agree_policyRules: [
        v => !!v || 'This field is required.',
      ],
    }
  },
  mounted() {
    this.getbusinessimage();
    this.AllCats();
    this.AllStates();
    this.getCities();
    this.allmaincats();
  },

  methods: {
    getbusinessimage(){
      axios.get('profile/details')
          .then((resp)=>{
            this.getimage = resp.data.profile_details;
          });
    },
    AllCats() {
      if (this.formData.cat_id) {
        axios.get(`subcategories/${this.formData.cat_id}`)
            .then((resp) => {
              this.allcategories = resp.data;
            })
            .catch(error => {
              console.log(error);
            });
      } else {
        this.allcategories = [];
      }
    },
    allmaincats() {
      axios.get('maincategory')
          .then((resp) => {
            this.allmaincategories = resp.data.maincategories;
          });
    },
    AllStates(){
      axios.get('allstates')
          .then((resp)=>{
            this.allstates = resp.data.locations;
          });
    },
    getCities() {
      if (this.stateId) {
        axios.get(`states/${this.stateId}`)
            .then(response => {
              this.cities = response.data;
            })
            .catch(error => {
              console.log(error);
            });
      } else {
        this.cities = [];
      }
    },

    userNameVerify(){ // verify username
        if(this.formData.username.length >= 8){
          axios.post('user/verify', {'username':this.formData.username})
              .then((resp) => {
                if(resp.data.status==400){
                    this.userNameVerifyMessage = resp.data.message;
                }else{
                    this.userNameVerifyMessage = "";
                }
          });
        }
    },
    emailVerify(){ // verify email
      if(/.+@.+/.test(this.formData.email)){
          axios.post('user/verify', {'email':this.formData.email})
              .then((resp) => {
                if(resp.data.status==400){
                    this.emailVerifyMessage = resp.data.message;
                }else{
                    this.emailVerifyMessage = "";
                }
          });
        }
    },
    phoneVerify(){ // verify phone
        if(this.formData.mobile_phone.length == 10){
            axios.post('user/verify', {'mobile_phone':this.formData.mobile_phone})
                .then((resp) => {
                  if(resp.data.status==400){
                      this.phoneVerifyMessage = resp.data.message;
                  }else{
                      this.phoneVerifyMessage = "";
                  }
            });
        }
    },

    async BusinessformSubmit() {
      const config = {
        headers: { 'content-type': 'multipart/form-data' }
      }
      await axios.post('business/register', this.formData, config)
          .then((resp) => {
            this.snackbar = true;
            this.text = resp.data.message;
            this.timeout = 3000;
            this.$router.push({name: 'businesslogin'});
          }).catch(error => {
            // var data = {...error.response};
            var data = error.toJSON();
            if(data.status == 400){
              this.snackbar = true;
              this.text = "something went wrong.";
              this.timeout = 3000;
            }
          });
      this.$refs.form.validate();
    },
  },
  
}
</script>

<style scoped>
.v-card__text .col {
  padding: 0 5px;
}
.v-label.theme--light {
  font-size: 14px;
}
</style>